import React, { useEffect, useState } from "react"
import ImageCarouselStyles from "../styles/ImageCarousel.module.css"
import BackgroundImage from "gatsby-background-image"
import CarouselImageSupplier from "../components/CarouselImageSupplier"

function ImageCarousel({ LeftText }) {
  const [timer, setTimer] = useState({ seconds: 6000, intervalID: 0 })
  const [index, setIndex] = useState(0)
  const [toggle, setToggle] = useState(0)
  const Info = CarouselImageSupplier()

  useEffect(() => {
    const intervalID = setInterval(() => {
      setToggle(state => !state)
      setIndex(state => (state + 1) % Info.length)
    }, timer.seconds)
    setTimer(state => {
      return { ...state, intervalID: intervalID }
    })
    return () => {
      clearInterval(timer.intervalID)
    }
  }, [])

  return (
    <div className={ImageCarouselStyles.container}>
      <BackgroundImage
        Tag="div"
        className={ImageCarouselStyles.carouselBG}
        fluid={Info[index].src}
      />

      {/* <div className={ImageCarouselStyles.ContentLeft}>
        <h2>{LeftText}</h2>
        <p>Hello</p>
      </div> */}
      <div className={ImageCarouselStyles.ContentRight}>
        <div className={ImageCarouselStyles.card}>
          <div
            style={{ alignSelf: Info[index].title ? "center" : "flex-end" }}
            className={ImageCarouselStyles.cardHeader}
          >
            {Info[index].title || `${index + 1}.`}
          </div>
          <div className={ImageCarouselStyles.cardSub}>
            {Info[index].subtitle}
          </div>
          <p>{Info[index].text}</p>
        </div>
      </div>
      <div className={ImageCarouselStyles.carouselShutters}></div>
    </div>
  )
}

export default ImageCarousel
