
import { graphql, useStaticQuery } from "gatsby"


const CarouselImageSupplier = () => {
  const { CarouselImages } = useStaticQuery(graphql`
    query {
      CarouselImages: allFile(
        filter: {
          sourceInstanceName: { eq: "Images" }
          relativeDirectory: { eq: "Carousel" }
          extension: { eq: "jpg" }
          childImageSharp: { fluid: {} }
        },
        sort: {fields: name}
      ) {
        edges {
          node {
            childImageSharp {
              fluid(maxWidth: 300, quality: 100) {
                originalName
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
      }
    }
  `)
  const Info = [
    {
      title: "TITLE 1",
      subtitle: "Subtitle 1",
      text: "Sample 1 Test Text",
    },
    {
      title: "TITLE 2",
      subtitle: "Subtitle 2",
      text: "text1 something something naturue something",
    },
  ]
 
  return CarouselImages.edges.map((edge, index) => {
    return { src: edge.node.childImageSharp.fluid, ...Info[index] }
  })
}

export default CarouselImageSupplier
